import sortItems from "./sort.js";
import runScroller from "./runScroll.js";
import "./styles/checker-container.css";

///     
/// document.querySelectorAll('span[clk_trigger]')[3].click()     
//  document.querySelectorAll(".SearchMainFilters_SearchMainFilters__sortContainer__jpst2 button")[3].click
export default function customSorter() {
  // messge       
  const message = chrome.i18n.getMessage("sortByLocale");
  const sortContainerOnAli = document.querySelector(".sort");
  const checkerContainer = document.createElement("div");
  checkerContainer.classList.add("ali_ext_checker-container");
  const checkerText = document.createElement("span");
  checkerText.textContent = message;
  checkerContainer.append(checkerText);
  sortContainerOnAli.append(checkerContainer);

  //  
  const sortButtons = document.querySelectorAll(
    ".SearchMainFilters_SearchMainFilters__sortContainer__jpst2 button"
  );
  //    
  const sortButtonsSpans = document.querySelectorAll(
    "span[clk_trigger].sort-item"
  );

  checkerContainer.addEventListener("click", () => {
    let standartSortNodes = false;
    if (sortButtonsSpans.length > 0) {
      standartSortNodes = sortButtonsSpans;
    } else {
      standartSortNodes = sortButtons;
    }

    if (!standartSortNodes || standartSortNodes.length === 0) {
      // console.log("    ");
      return;
    }

    //      .  .  k  "icon"
    //  ,  iconUp,       ,iconDown -    
    //  // ae_object_value = "price(lowest)";
    let checkStatusSortBy = false;
    if (
      standartSortNodes[3]
        .querySelector("svg")
        .className.baseVal.includes("iconDown")
    ) {
      checkStatusSortBy = true;
    } else if (
      standartSortNodes[3].getAttribute("ae_object_value") === "price(lowest)"
    ) {
      checkStatusSortBy = true;
    }

    if (!checkStatusSortBy) {
      window.aliFilter.runScroll = true;
      runScroller();
      // console.log("  ,  ");
      return;
    } else if (checkStatusSortBy === undefined) {
      // console.log("     ");
      return;
    }

    //      ,     
    //    Null       
    //        
    standartSortNodes[3].click();
    let timerCountry = setTimeout(async function tickCountry() {
      //   

      let standartPreloader = false;

      if (
        document.querySelector(
          "SearchWrap_LoadOverlay__loadOverlayShow__hhorj.SearchWrap_LoadOverlay__loadOverlay__hhorj"
        )
      ) {
        standartPreloader = document.querySelector(
          "SearchWrap_LoadOverlay__loadOverlayShow__hhorj.SearchWrap_LoadOverlay__loadOverlay__hhorj"
        );
      } else if (document.querySelector(".next-overlay-wrapper.opened")) {
        standartPreloader = document.querySelector(
          ".next-overlay-wrapper.opened"
        );
      }

      //      c  SearchProductFeed_Alert__warning__8d2ff
      let aluCrashedMessage = document.querySelector(
        ".SearchProductFeed_Alert__warning__8d2ff"
      );
      if (aluCrashedMessage !== null) {
        // console.log("    ");
        return;
      }
      if (standartPreloader === null || !standartPreloader) {
        // console.log(" ,  ");
        window.aliFilter.runScroll = true;
        runScroller();
        return;
      } else {
        timerCountry = setTimeout(tickCountry, 300); // (*)
      }
    }, 300);
  });
}
// ae_object_value = "price(highest)";
